import axios, {AxiosResponse} from 'axios';
import {computed, observable} from 'mobx';
import {IMatch, IMatchList, IMatchListProps} from '../../@types';

export class MatchListStore implements IMatchList {
  public id: string;
  @observable public isLoaded: boolean = false;
  @observable public list: IMatch[] = [];
  public readonly type: string;

  constructor(props: IMatchListProps) {
    this.type = props.type;
    this.id = props.id;
    this.fetchList();
  }

  private fetchList() {
    axios.get(this.matchListUrl)
      .then((res: AxiosResponse) => {
        this.list = [...res.data.results];
        this.isLoaded = true;
      });
  }

  @computed
  private get matchListUrl(): string {
    return `/api/results/${this.id}`;
  }
}
